
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {Decimal} from 'decimal.js'
import {exchangeList} from '@/modules/exchange/api/get_exchange'
import {showloadings, hideloadings} from '@/utils/load'

export default {
name: 'historyorders',
data() {
return {
buy: 'buy',
exchangeListData: [],
historyState: 'history',
tips: true
}
},
created() {
},
beforeRouteEnter(from, to, next) {
next(vm => {
vm.getExchangeList(vm.historyState)
})
},
mounted() {
},
methods: {
getExchangeList() {
showloadings()
exchangeList(this.historyState).then(res => {
if (res.status === 200) {
const data = res.data
if (data.code === '200') {
this.exchangeListData = data.data
hideloadings()
}
}
})
}
},
filters: {
toUpper(val) {
return val.toUpperCase()
},
strIntercept(val) {
return val.substr(0, 19)
},
numberIntercept(val) {
return new Decimal(val).toFixed(6)
}
},
watch: {
},
components: {
Group,
XHeader,
Cell
}
}



